<template>
  <div class="root">
    <div v-for="confirmation in confrimations" :key="confirmation.id">
      <div class="confirmation">
        <img :src="confirmation.icon" width="40" height="40">

        <span class="vertical_block">
          <span>{{ confirmation.receiving }}</span>

          <span class="horizontal_block">
            <button @click="accept_offer(confirmation.id, confirmation.key)">Принять</button>
            <button @click="decline_offer(confirmation.id, confirmation.key)">Отклонить</button>
          </span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    confrimations: Array,
    accountName: String
  },

  methods: {
    accept_offer: function (id, key) {
      this.$parent.$parent.ipc.send('accept_offer', {name: this.accountName, id: id, key: key})
    },

    decline_offer: function (id, key) {
      this.$parent.$parent.ipc.send('decline_offer', {name: this.accountName, id: id, key: key})
    }
  }
}
</script>

<style scoped>
.root {
  width: 360px;
  height: 250px;
  background-color: #2c3e50;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);

  margin: 10px 0px 5px 0px;

  border-radius: 15px;

  z-index: 100;

  overflow: hidden;
  overflow-y: auto;

  padding: 10px 0 10px 0;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.confirmation {
  width: 360px;
  height: 50px;

  margin-bottom: 5px;

  background-color: rgba(0, 0, 0, .3);

  display: flex;
  justify-content: space-around;
  align-items: center;

  color: #ecf0f1;
}

.confirmation .vertical_block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
</style>



