<template>
  <div id="app">
    <EditModal v-show="editModalAccountID != null" />
    <NewAccountModal v-show="createMode != false" />
    <Header/>
    <Body/>
  </div>
</template>

<script>
import Header from './components/Header'
import Body from './components/Body'
import EditModal from './components/EditModal'
import NewAccountModal from './components/NewAccountModal'

export default {
  name: 'App',
  components: {
    Header,
    Body,
    EditModal,
    NewAccountModal
  },
  data: function () {
    return {
      editMode: false,
      editModalAccount: {},
      editModalAccountID: null,

      createMode: false,

      ipc: window.require('electron').ipcRenderer,
      electron: window.require('electron')
    }
  },

  created: function () {
    this.ipc.on('login_error', function (event, data) {
      // eslint-disable-next-line
      new Notification('Ошибка при входе', {body: data})
    })

    this.ipc.on('enable_2fa_error', function (event, data) {
      // eslint-disable-next-line
      new Notification('Ошибка при включении 2FA аутентификации', {body: data})
    })

    this.ipc.on('notification', function (event, data) {
      // eslint-disable-next-line
      new Notification(data.title, {body: data.body})
    })
  }
}

</script>

<style>

body {
  user-select: none;
  outline: none;
  overflow: hidden;
  margin: 0px;
  background-color: #34495e;
}

#app {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 400px;
  height: 600px;
  background-color: #34495e;

}

.fade-enter-active {
  animation: bounceInDown .5s;
}

.fade-leave-active {
  animation: bounceOutDown .5s;
}

</style>



