<template>
  <div class="root">
    <div class="edit_form">
      <i class="fa fa-times close_modal" @click="close_modal()"></i>

      <div class="form">
        <label for="name">Имя аккаунта</label>
        <input type="text" name="name" v-model="$parent.editModalAccount.name">
        <label for="shared_secret">SHARED_SECRET</label>
        <input type="text" name="shared_secret" v-model="$parent.editModalAccount.shared_secret">
        <label for="identity_secret">IDENTITY_SECRET</label>
        <input type="text" name="identity_secret" v-model="$parent.editModalAccount.identity_secret">

        <button @click="save_data()"><i class="fa fa-save fa-2x"></i></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    save_data: function () {
      this.$parent.ipc.send('edit_acc', { id: this.$parent.editModalAccountID, account: this.$parent.editModalAccount })

      this.close_modal()
    },

    close_modal: function () {
      this.$parent.editModalAccountID = null
      this.$parent.editModalAccount = {}
    }
  }
}
</script>

<style scoped>
.root {
  width: 400px;
  height: 600px;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);

  z-index: 101;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: .5s;
}

.edit_form {
  width: 300px;
  height: 400px;
  background-color: #34495e;
  border: 5px solid #2c3e50;
  border-radius: 15px;
  position: relative;

  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.close_modal {
  position: absolute;
  top: 5px;
  right: 5px;
  color: #e74c3c;
  cursor: pointer;
}

.close_modal:hover {
  font-size: 18px;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;

  color: white;
  margin-top: 30px;
}

.form input {
  width: 250px;

  border-radius: 5px;
  outline: none;

  padding-left: 15px;
  padding-top: 5px;
  font-size: 18px;
}

.form button {
  margin-top: 5px;
  padding: 5px 50px 5px 50px;
}

</style>



