<template>
  <div class="root">
    <div class="create_account">
      <i class="fa fa-times close_modal" @click="close_modal()"></i>

      <div class="buttons" v-if="status < 3">
        <button class="add_method" @click="status = 1" :id="status === 2 ? 'first' : 'second'">Аккаунт с 2FA</button>
        <button class="add_method" @click="status = 2" :id="status === 1 ? 'first' : 'second'">Аккаунт без 2FA</button>
      </div>

      <div class="form" v-if='status == 1'>
        <input type="text" class="form_field" v-model="login" placeholder="Логин">
        <input type="text" class="form_field" v-model="password" placeholder="Пароль">
        <input type="text" class="form_field" v-model="shared_secret" placeholder="SHARED_SECRET">
        <input type="text" class="form_field" v-model="identity_secret" placeholder="IDENTITY_SECRET">

        <button @click="create_with2fa()"><i class="fa fa-arrow-right fa-3x"></i></button>
      </div>

      <div class="form" v-if='status == 2'>
        <input type="text" class="form_field" v-model="login" placeholder="Логин">
        <input type="text" class="form_field" v-model="password" placeholder="Пароль">
        <input v-show="show_mailcode" type="text" class="form_field" v-model="email_code" placeholder="Код Steam Guard">

        <button @click="create()"><i class="fa fa-arrow-right fa-3x"></i></button>
      </div>

      <div class="form" v-if='status == 3'>
        <input type="text" class="form_field" v-model="sms_code" placeholder="СМС код">

        <button @click="finalize()"><i class="fa fa-arrow-right fa-3x"></i></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      status: 1,
      login: null,
      password: null,
      shared_secret: null,
      identity_secret: null,
      email_code: null,
      sms_code: null,

      show_mailcode: false
    }
  },

  methods: {
    close_modal: function () {
      this.$parent.createMode = false
    },

    create_with2fa: function () {
      this.$parent.ipc.send('create_with2fa', [this.login, this.password, this.shared_secret, this.identity_secret])
    },

    create: function () {
      this.$parent.ipc.send('create_wout2fa', [this.login, this.password, this.email_code])
    },

    finalize: function () {
      this.$parent.ipc.send('finalize', [this.login, this.sms_code])
    }
  },

  created: function () {
    var self = this

    this.$parent.ipc.on('account_added', function (event, data) {
      self.$parent.createMode = false

      self.status = 1
      self.login = null
      self.password = null
      self.shared_secret = null
      self.identity_secret = null
      self.email_code = null

      // eslint-disable-next-line
      new Notification('Аккаунт успешно добавлен')
    })

    this.$parent.ipc.on('show_mailcode_field', function (event) {
      self.show_mailcode = true
    })

    this.$parent.ipc.on('show_smsfield', function (event) {
      self.status = 3
    })
  }
}
</script>

<style scoped>
.root {
  width: 400px;
  height: 600px;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);

  z-index: 101;

  display: flex;
  justify-content: center;
  align-items: center;

}

.create_account {
  width: 300px;
  height: 400px;
  background-color: #34495e;
  border: 5px solid #2c3e50;
  border-radius: 15px;
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.close_modal {
  position: absolute;
  top: 5px;
  right: 5px;
  color: #e74c3c;
  cursor: pointer;
}

.close_modal:hover {
  font-size: 18px;
}

.buttons {
  margin-top: 30px;
}

.add_method {
  font-family: 'Roboto';
  font-size: 16px;

  outline: none !important;
}

#first {
  background-color: #34495e;
  color: #ecf0f1;
  border: 3px solid rgba(236, 240, 241, 0.3);

  cursor: pointer;
}

#first:hover {
  border-color: rgba(236, 240, 241, 0.7);
}

#second {
  background-color: #34495e;
  color: #ecf0f1;
  border: 3px solid rgba(44, 62, 80, 0.7);
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;

  color: white;
  margin-top: 30px;
}

.form_field {
  font-family: 'Roboto';

  margin-bottom: 5px;
  padding: 5px;
  font-size: 18px;
  border-radius: 5px;

  outline: none;
}
</style>



