<template>
  <div class="root">
    <span v-if="$parent.editMode" class="account_cell add_new" @click="$parent.createMode = true">
      <i class="fa fa-plus fa-3x"></i>
    </span>

    <div :key="account.name" v-for="(account, index) in accounts_vue" class="account_field">
      <span class="account_cell existed">
        <h3 @click="account.confirm_modal = !account.confirm_modal" class="name">{{ account.name }}</h3>
        <span class="code" @click="gcode_toclipboard(account.auth_code)">{{ account.auth_code }}  <i class="fa fa-copy"></i></span>

        <span class="icons_bar">
          <i v-show="$parent.editMode" class="fa fa-pencil-alt edit_acc" @click="edit_modal(index)"></i>
          <i class="fa fa-sync" @click="relogin(account.name)"></i>
          <i v-show="$parent.editMode" class="fa fa-times delete_acc" @click="delete_acc(index)"></i>
        </span>
      </span>

      <transition name="fade">
        <confirm-template v-show="account.confirm_modal" :confrimations="account.confirmations" :account-name="account.name" />
      </transition>
    </div>
  </div>
</template>

<script>
import Confirmations from './Confirmations'

export default {
  components: {
    'confirm-template': Confirmations
  },

  data: function () {
    return {
      accounts_vue: [],
      accounts_data: this.$parent.electron.remote.getGlobal('accounts'),

      get_guard_code: this.$parent.electron.remote.getGlobal('generate_auth_code')
    }
  },

  methods: {
    delete_acc: function (id) {
      if (!confirm('Вы уверены, что хотите удалить этот аккаунт?')) return

      this.accounts_vue.splice(id, 1)
      this.$parent.ipc.send('delete_acc', id)
    },

    edit_modal: function (id) {
      this.$parent.editModalAccount = this.accounts_vue[id]
      this.$parent.editModalAccountID = id
    },

    relogin: function (login) {
      this.$parent.ipc.send('login', login)
    },

    reload_gcodes: function () {
      for (let i in this.accounts_vue) {
        this.accounts_vue[i].auth_code = this.get_guard_code(this.accounts_vue[i].shared_secret)
      }
    },

    gcode_toclipboard: function (code) {
      this.$parent.electron.clipboard.writeText(code)
      // eslint-disable-next-line
      new Notification('Уведомление', { body: 'Гуард код ' + code + ' скопирован в буффер обмена' })
    },

    sync_accounts: function () {
      for (let i in this.accounts_data) {
        this.accounts_data[i].confirm_modal = false
        this.accounts_data[i].confirmations = []

        this.$parent.ipc.send('login', this.accounts_data[i].name)

        this.$set(this.accounts_vue, i, Object.assign({}, this.accounts_data[i]))
      }
    },

    get_confirmations: function () {
      for (let i in this.accounts_vue) {
        this.$parent.ipc.send('get_confirmations', this.accounts_vue[i].name)
      }
    }
  },

  created: function () {
    this.sync_accounts()
    this.reload_gcodes()

    var self = this
    setInterval(function () {
      self.reload_gcodes()
      self.get_confirmations()
    }, 30000)

    this.$parent.ipc.on('reload_accounts', function (event, data) {
      self.accounts_data = self.$parent.electron.remote.getGlobal('accounts')
      self.sync_accounts()
    })

    this.$parent.ipc.on('new_confirmations', function (event, data) {
      for (let i in self.accounts_vue) {
        let account = self.accounts_vue[i]

        if (account.name === data.name) {
          account.confirmations = data.confirmations
          break
        }
      }
    })
  }
}
</script>

<style scoped>
.root {
  width: 100%;
  height: 540px;

  overflow-y: scroll;
  overflow-x: hidden;

}

.root::-webkit-scrollbar-track {
  border: 1px solid #000;
  padding: 2px 0;
  background-color: #404040;
}

.root::-webkit-scrollbar {
  width: 10px;
}

.root::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #737272;
  border: 1px solid #000;
}

.account_field {
  display: flex;

  flex-direction: column;
  align-items: center;

  z-index: 1;
}

.account_cell {
  width: 390px;
  height: 40px;

  display: flex;
  justify-content: space-around;
  align-items: center;

  position: relative;
}

.existed {
  background-color: #2c3e50;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  transition: .1s;
  margin-top: 5px;
  color: white;
}

.existed .code {
  background-color: #34495e;
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
  transition: .1s;
}

.existed .code:hover {
  font-size: 18px;
}

.existed .code:active {
  background-color: #27ae60;
}

.add_new {
  color: rgba(236, 240, 241, 0.5);
  background-color: rgba(236, 240, 241, 0.3);

  margin: 5px;
  width: 95%;
  height: 60px;

  border-radius: 5px;
  border: 5px solid rgba(236, 240, 241, 0.5);

  cursor: pointer;

  transition: .2s;
}

.add_new:hover {
  color: rgba(236, 240, 241, 1);
  border-color: rgba(236, 240, 241, 1);
  background-color: rgba(39, 174, 96, 0.1);
}

.icons_bar {
  position: absolute;
  top: 3px;
  right: 5px;

  width: 40px;
  text-align: right;
}

.icons_bar i {
  cursor: pointer;
}

.icons_bar i:hover {
  font-size: 18px;
}

.delete_acc {
  color: #e74c3c;
}

.confirm_modal {
  width: 360px;
  height: 400px;
  background-color: #2c3e50;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);

  margin: 10px 0px 5px 0px;

  border-radius: 15px;

  z-index: 100;
}
</style>



